import React, { Fragment, useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import { Reports_Services } from '../../queries/reports.query'
import { Button, Typography } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import moment from 'moment';
import Moment from 'react-moment';
import NumberFormat from 'react-number-format';
import config from '../../config'
import axios from 'axios'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import clsx from 'clsx';
import MenuItem from '@material-ui/core/MenuItem';
import { getRequestTypes, listCustomers, listCenterOfAttention, requestsReport, printRequest, printRequests } from '../../api/api';
import jwtDecode from 'jwt-decode';
import Tooltip from '@material-ui/core/Tooltip'
import { BsClockHistory } from 'react-icons/bs';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import DialogContentText from '@material-ui/core/DialogContentText';
import CircularProgress from '@material-ui/core/CircularProgress';

import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import { FaPrint } from "react-icons/fa";
import { AiOutlineCheckCircle, AiOutlineCloseCircle } from "react-icons/ai";
import { RiSendPlaneLine } from "react-icons/ri";
import { MdOutlineScheduleSend } from "react-icons/md";
import { AiOutlineCloudDownload } from "react-icons/ai";
import { ToastContainer, toast } from 'react-toastify';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        height: 250,
        minWidth: 290
    },
    input: {
        display: "flex",
        padding: 0,
        height: "auto"
    },
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
        marginTop: 10,
        margin: 5
    },
    button:
    {
        margin: theme.spacing(1),
    },
    formControl: {
        margin: theme.spacing(1),
    },
    icon: {
        width: 24,
        height: 24
    }
}))

const ReportRequests = props => {

    moment().locale('es')

    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(1);
    const [totalFields, setTotalFields] = useState(0)

    const classes = useStyles()
    const [startDate, setStartDate] = useState(new Date())
    const [endDate, setEndDate] = useState(new Date())
    const [pdf, setPdf] = useState(false)
    const [dataPdf, setDataPdf] = useState('')

    const [informations, setInformations] = useState([])
    const [total, setTotal] = useState(0)
    const [serviceType, setServiceType] = useState(1)
    const [serviceStatus, setServiceStatus] = useState('1')
    const [serviceTypes, setServiceTypes] = useState([])
    const [customers, setCustomers] = useState([])
    const [customer, setCustomer] = useState("")
    const [centerOfAttentions, setCenterOfAttentions] = useState([])
    const [centerOfAttention, setCenterOfAttention] = useState("")
    const [openHistory, setOpenHistory] = useState(false);
    const [attention, setAttention] = useState(null);
    const [errorCustomer, setErrorCustomer] = useState(false);
    const [errorTextCustomer, setErrorTextCustomer] = useState('');
    const [errorCenterOfAttention, setErrorCenterOfAttention] = useState(false);
    const [errorTextCenterOfAttention, setErrorTextCenterOfAttention] = useState('');
    const [isPrinting, setIsPrinting] = useState(false);

    const serviceStatuses = [{
        id: "1",
        text: "Todas"
    }, {
        id: "accept",
        text: "Aceptada"
    }, {
        id: "reject",
        text: "Rechazada"
    }, {
        id: "resend",
        text: "Reenviada"
    }]

    function prepend(value, array) {
        var newArray = array.slice();
        newArray.unshift(value);
        return newArray;
    }

    useEffect(() => {

        getRequestTypes().then(response => {
            let data = {
                "_id": "1",
                "type": "Todas",
                "status": "active",
                "tag": ""
            }
            let requestTypes = prepend(data, response.requestTypes)
            setServiceTypes(requestTypes);
        })

        listCustomers({}).then(response => {
            setCustomers(response.customers);
        })

    }, []);

    const onChangeServiceType = event => {
        setServiceType(event.target.value)
    }
    const onChangeCenterOfAttention = event => {
        setCenterOfAttention(event.target.value)
    }
    const onChangeCustomer = event => {
        setCustomer(event.target.value)
        listCenterOfAttention({}).then(response => {
            setCenterOfAttentions(response.centersOfAttention);
        })
    }
    const onChangeServiceStatus = event => {
        setServiceStatus(event.target.value)
    }
    const onChangeStartDate = date => {
        setStartDate(date)
    }
    const onChangeEndDate = value => {
        setEndDate(value)
    }
    const handleChangePage = (_, newPage) => {

        let data = {
            start: (newPage * rowsPerPage),
            end: rowsPerPage,
            paginate: true,
            startDate,
            endDate,
            customer,
            centerOfAttention,
            serviceType,
            serviceStatus,
            source: 'web'
        }
        setPage(newPage);
        requestsReport(data).then(response => {
            setInformations(response.requests);
            setTotalFields(response.count)
        });
    }
    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = {
            start: 0,
            end: event.target.value,
            paginate: true,
            startDate,
            endDate,
            customer,
            centerOfAttention,
            serviceType,
            serviceStatus,
            source: 'web'
        }
        requestsReport(data).then(response => {
            setInformations(response.requests);
            setTotalFields(response.count)
        });
    }
    const validateForm = () => {

        let errors = 0
        const user = jwtDecode(localStorage.getItem('token'))


        if (user.role !== "5a046fe9627e3526802b3847")
            errors += validateCustomer()


        if (user.role == "627d968a96a6a6b76f30c7e9") {
            errors += validateCenterOfAttention()
        }

        return errors
    }
    const validateCustomer = (value) => {
        let text = value !== undefined ? value : customer

        if (text == null || text.toString().trim() === '') {
            setErrorCustomer(true)
            setErrorTextCustomer('Este campo es requerido')
            return 1
        }
        else {
            setErrorCustomer(false)
            setErrorTextCustomer('')
            return 0
        }
    }
    const validateCenterOfAttention = (value) => {
        let text = value !== undefined ? value : centerOfAttention

        if (text == null || text.toString().trim() === '') {
            setErrorCenterOfAttention(true)
            setErrorTextCenterOfAttention('Este campo es requerido')
            return 1
        }
        else {
            setErrorCenterOfAttention(false)
            setErrorTextCenterOfAttention('')
            return 0
        }
    }
    const onPrint = () => {

        setIsPrinting(true);
        let data = {
            start: 0,
            end: rowsPerPage,
            paginate: true,
            startDate,
            endDate,
            customer,
            centerOfAttention,
            serviceType,
            serviceStatus,
            source: 'web'
        }
        printRequests(data).then(response => {
            setIsPrinting(false);
            if (response.status === "success") {
                let pdfData = 'data:application/pdf;base64,' + response.base64
                setDataPdf(pdfData)
                setPdf(true)
            }
        }).catch(e => {
            console.log(e)
            setIsPrinting(false);
        })
    }
    const onPrintSingle = id => () => {

        console.log("id ", id)
        setIsPrinting(true);

        printRequest({ id }).then(response => {
            setIsPrinting(false);
            if (response.status === "success") {
                // window.open(`${config.urlPdf}${response.fileName}.pdf`)
                let pdfData = 'data:application/pdf;base64,' + response.base64
                setDataPdf(pdfData)
                setPdf(true)
            }
        }).catch(e => {
            console.log(e)
            setIsPrinting(false);
        })
    }
    const onClosePdf = () => {
        setPdf(false)
    }
    const onSubmit = e => {

        let errores = validateForm()
        if (errores === 0) {

            let data = {
                start: 0,
                end: rowsPerPage,
                paginate: true,
                startDate,
                endDate,
                customer,
                centerOfAttention,
                requestType: serviceType,
                serviceStatus,
                source: 'web'
            }
            console.log(data)

            requestsReport(data).then(response => {
                console.log(response)
                setTotalFields(response.count)
                setInformations(response.requests)
            })
        }
    }
    const onOpenModalHistory = attention => () => {
        setOpenHistory(true);
        setAttention(attention);
    }
    const onCloseModalHistory = () => {
        setOpenHistory(false);
    }
    const getStatus = item => {
        switch (item.status) {
            case "created":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Enviado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>


            case "resend":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Reenviado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>

            case "reject":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Rechazado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>

            case "finished":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Rechazado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>

            case "accept":
                return <div className='wrapper__history'>
                    <div className='statusHistory'>Aceptado</div>
                    <div className='dateHistory'>{moment(item.date).format("YYYY-MM-DD HH:mm:ss")}</div>
                </div>



        }
    }
    const getIcon = status => {
        switch (status) {
            case "reject":
                return <AiOutlineCloseCircle style={{ fontSize: 32, color: "red" }} />

            case "created":
                return <RiSendPlaneLine style={{ fontSize: 32, color: "red" }} />

            case "accept":
                return <AiOutlineCheckCircle style={{ fontSize: 32, color: "green" }} />

            case "resend":
                return <MdOutlineScheduleSend style={{ fontSize: 32, color: "silver" }} />

            case "finished":
                return <MdOutlineScheduleSend style={{ fontSize: 32, color: "silver" }} />

        }

    }

    return (
        <Fragment>
            <ToastContainer />

            <Container maxWidth="lg">
                <Paper className={classes.paper}>
                    <form encType={'multipart/form-data'}
                        onSubmit={e => {
                            e.preventDefault()
                            onSubmit()
                        }} >
                        <Grid container >

                            <Grid item xs={12} sm={12}>
                                <Typography><b>Reporte de solicitudes</b></Typography>
                            </Grid>
                            <Grid item xs={12} sm={3}>
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <KeyboardDatePicker
                                        disableFuture
                                        format="yyyy-MM-dd"
                                        margin="dense"
                                        id="date-picker-inline"
                                        label="Fecha inicial"
                                        value={startDate}
                                        onChange={onChangeStartDate}
                                        KeyboardButtonProps={{
                                            'aria-label': 'change date',
                                        }}
                                    />
                                </MuiPickersUtilsProvider>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <KeyboardDatePicker
                                        format="yyyy-MM-dd"
                                        margin="dense"
                                        id="date-picker-inline"
                                        label="Fecha final"
                                        value={endDate}
                                        onChange={onChangeEndDate}
                                        KeyboardButtonProps={{
                                            'aria-label': 'change date',
                                        }}
                                    />
                                </MuiPickersUtilsProvider>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Cliente"
                                    error={errorCustomer}
                                    helperText={errorTextCustomer}
                                    value={customer}
                                    onChange={onChangeCustomer}>
                                    {
                                        customers.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.name}
                                            </MenuItem>))
                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Centro de atencin"
                                    error={errorCenterOfAttention}
                                    helperText={errorTextCenterOfAttention}
                                    value={centerOfAttention}
                                    onChange={onChangeCenterOfAttention}>
                                    {
                                        centerOfAttentions.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.title}
                                            </MenuItem>))
                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Tipo de atencin"
                                    value={serviceType}
                                    onChange={onChangeServiceType}>
                                    {

                                        serviceTypes.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.type}
                                            </MenuItem>))
                                    }
                                </TextField>
                            </Grid>


                            <Grid item xs={2} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Estado de la atencin"
                                    value={serviceStatus}
                                    onChange={onChangeServiceStatus}>
                                    {
                                        serviceStatuses.map(option => (
                                            <MenuItem key={option.id} value={option.id} >
                                                {option.text}
                                            </MenuItem>))

                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={2} sm={12}>
                                <Button type="submit" variant="contained" color='primary' style={{ marginTop: 20 }}>Consultar</Button>
                            </Grid>
                        </Grid>
                    </form>
                </Paper>

            </Container>
            <Paper className={classes.paper}>
                <TableContainer className={classes.container} style={{ width: "100%" }}>
                    <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Fecha</TableCell>
                                <TableCell>Tipo</TableCell>
                                <TableCell>Cliente</TableCell>
                                <TableCell>Nombre</TableCell>
                                <TableCell>Descripcin</TableCell>
                                <TableCell>Centro de atencin</TableCell>
                                <TableCell width={30}>Historial</TableCell>
                                <TableCell width={30}>Imprimir</TableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                informations.map(service => {
                                    return (
                                        <TableRow hover key={service._id}>
                                            <TableCell><Moment format="ll">{new Date(service.date)}</Moment></TableCell>
                                            <TableCell>{service.request_type.type}</TableCell>
                                            <TableCell>{service.customer.name}</TableCell>
                                            <TableCell>{service.title}</TableCell>
                                            <TableCell>{service.description}</TableCell>
                                            <TableCell>{service.centerOfAttention ? service.centerOfAttention.title : ""}</TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                {
                                                    service.descriptions.length > 0 ? <Tooltip title="Hisotoria" placement="top">
                                                        <div>
                                                            <BsClockHistory className={"icon"} onClick={onOpenModalHistory(service)} />
                                                        </div>
                                                    </Tooltip> : ""
                                                }
                                            </TableCell>
                                            <TableCell className={classes.iconWidth}>
                                                <Tooltip title="Descargar" placement="top">
                                                    {
                                                        service.downloading ?
                                                            <div className='circularProgress'>
                                                                <CircularProgress size={16} />
                                                            </div> :
                                                            <div>
                                                                <AiOutlineCloudDownload className={"icon"} onClick={onPrintSingle(service._id)} />
                                                            </div>
                                                    }


                                                </Tooltip>
                                            </TableCell>

                                        </TableRow>
                                    );
                                })}
                        </TableBody>
                        {
                            informations.length > 0
                                ? <TableFooter>
                                    <TableRow hover>
                                        <TableCell>
                                            {
                                                !isPrinting ?
                                                    <FaPrint className={classes.icon} onClick={onPrint} />
                                                    : <CircularProgress size={16} />
                                            }
                                        </TableCell>
                                        <TableCell colSpan={8} align="right">
                                            <Typography variant="h3">
                                                <NumberFormat value={total} displayType={'text'} thousandSeparator={true} prefix={'$'} />
                                            </Typography>
                                        </TableCell>
                                    </TableRow>
                                </TableFooter>
                                : ''
                        }
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalFields}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>


            <Dialog
                maxWidth="lg"
                open={pdf}
                onClose={onClosePdf}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle id="draggable-dialog-title">Reporte de servicios</DialogTitle>
                <DialogContent>
                    <iframe src={dataPdf} title="Imprimir" height="900px" width="600px" ></iframe>
                </DialogContent>
                <DialogActions>

                    <Button onClick={onClosePdf} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openHistory}
                onClose={onCloseModalHistory}>
                <DialogTitle >
                    Historia
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <List className={classes.root}>
                            {
                                attention ?
                                    attention.descriptions.map(item => {
                                        return (
                                            <ListItem alignItems="flex-start">
                                                <ListItemIcon>
                                                    {getIcon(item.status)}
                                                </ListItemIcon>
                                                <ListItemText
                                                    primary={getStatus(item)}
                                                    secondary={item.description}
                                                />
                                            </ListItem>

                                        );
                                    })
                                    : ""
                            }
                        </List>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseModalHistory} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>
    )
}
export default ReportRequests 